body {
  background-color: rgb(232, 212, 200);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.text-container {
  width: 500px;
  height: 500px;
  margin: auto;

  border-color: white;
  border-style: solid;
  border-width: 2px;
}
.header {

}

.header h1 {

    color: rgb(0, 0, 0);
}

.navBar {
    background-color: rgb(222, 203, 187);
    margin-left: 10px;
}

button {
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
}


.image {
    width: -webkit-max-content;
    width: max-content;
    border-style: solid;
    border-width: 5px;
    text-align: center;
    margin: auto;
    display: inline-block;
}

img {
    margin: 10px;
    width: 50px;
}
